import React, { useEffect, Suspense , useState } from "react";
import round from 'lodash/round'
import { Canvas, extend, useThree, useFrame } from "@react-three/fiber";
import {
  Effects,
  OrthographicCamera,
  BakeShadows,
  Preload,
  useProgress,
  PerformanceMonitor
} from "@react-three/drei";
import { UnrealBloomPass } from "three-stdlib";
import { Tower } from "./Hasaki";
import { OrbitControls } from "./OrbitControls.js";
import gsap from "gsap";

extend({ UnrealBloomPass });

const CameraController = () => {
  const { camera, gl } = useThree();
  const controls = new OrbitControls(camera, gl.domElement);
  var boutme = false;
  var autorotateTimeout;
  // restart autorotate after the last interaction & an idle time has passed
  controls.addEventListener("end", function () {
    autorotateTimeout = setTimeout(function () {
      if (boutme === true) {
        controls.autoRotate = false;
      } else {
        gsap.to(controls.object.position, {
          duration: 2,
          ease: "power1.inOut",
          x: 10,
          y: 2,
          z: 10
        });
        gsap.to(controls.target, {
          duration: 2,
          ease: "power1.inOut",
          x: 0,
          y: 0,
          z: 0
        });
        gsap.to(camera, {
          duration: 3,
          ease: "power1.inOut",
          zoom: 100,
          onUpdate: function () {
            camera.updateProjectionMatrix();
          }
        });

        controls.autoRotate = true;
      } //to not autorotate when in about page
    }, 5000);
  });
  // stop autorotate after the first interaction
  controls.addEventListener("start", function () {
    clearTimeout(autorotateTimeout);
    controls.autoRotate = false;
  });
  //  controls.addEventListener('change', function(){
  //   console.log( controls.object.zoom );
  // });

  document.addEventListener("keydown", onDocumentKeyDown, false);
  function onDocumentKeyDown(event) {
    var keyCode = event.code;

    if (keyCode === "KeyK") {
      boutme = true;
      controls.autoRotate = false;
      controls.enablePan = false;
      controls.enableRotate = false;
      controls.maxZoom = 950;
      controls.minZoom = 300;
      gsap.to(controls.target, {
        duration: 2,
        ease: "power1.inOut",
        x: -0.01372387778031494,
        y: 0.35755811331792507,
        z: -0.03441347518332228
      });
      gsap.to(controls.object.position, {
        duration: 2,
        ease: "power1.inOut",
        x: -0.05140252852651481,
        y: 4.3384061209172655,
        z: 13.594294024081638
      });
      gsap.to(camera, {
        duration: 3,
        ease: "power1.inOut",
        zoom: 450,
        onUpdate: function () {
          camera.updateProjectionMatrix();
        }
      });
    } else if (keyCode === "KeyL") {
      boutme = false;
      controls.enablePan = true;
      controls.enableRotate = true;
      controls.maxZoom = 250;
      controls.minZoom = 95;
      gsap.to(controls.object.position, {
        duration: 2,
        ease: "power1.inOut",
        x: 10,
        y: 2,
        z: 10
      });
      gsap.to(controls.target, {
        duration: 2,
        ease: "power1.inOut",
        x: 0,
        y: 0,
        z: 0
      });
      gsap.to(camera, {
        duration: 3,
        ease: "power1.inOut",
        zoom: 100,
        onUpdate: function () {
          camera.updateProjectionMatrix();
        }
      });
    } else if (keyCode === "KeyJ") {
      console.log(controls.target);
      console.log(controls.object);
      boutme = true;
    } else if (keyCode === "KeyO") {
      //left screen middle
      boutme = true;
      controls.autoRotate = false;
      controls.enablePan = false;
      controls.enableRotate = false;
      controls.maxZoom = 2200;
      controls.minZoom = 500;
      gsap.to(controls.object.position, {
        duration: 2,
        ease: "power1.inOut",
        x: -14.3362291314115,
        y: -0.10042007694260197,
        z: 0.18212376171082428
      });
      gsap.to(controls.target, {
        duration: 2,
        ease: "power1.inOut",
        x: 0,
        y: 0,
        z: 0
      });
      gsap.to(camera, {
        duration: 3,
        ease: "power1.inOut",
        zoom: 1550,
        onUpdate: function () {
          camera.updateProjectionMatrix();
        }
      });
    } else if (keyCode === "KeyH") {
      //left screen topper square
      boutme = true;
      controls.autoRotate = false;
      controls.enablePan = false;
      controls.enableRotate = false;
      controls.maxZoom = 2200;
      controls.minZoom = 950;

      gsap.to(controls.object.position, {
        duration: 2,
        ease: "power1.inOut",
        x: -14.337807265494403,
        y: -0.0634505242965081,
        z: 0.19761633541808538
      });
      gsap.to(controls.target, {
        duration: 2,
        ease: "power1.inOut",
        x: -0.021533266439470414,
        y: 0.518361483892234,
        z: 0.26768946061114063
      });
      gsap.to(camera, {
        duration: 3,
        ease: "power1.inOut",
        zoom: 2000,
        onUpdate: function () {
          camera.updateProjectionMatrix();
        }
      });
    } else if (keyCode === "KeyI") {
      //right top screens
      boutme = true;
      controls.autoRotate = false;
      controls.enablePan = false;
      controls.enableRotate = false;
      controls.maxZoom = 1360;
      controls.minZoom = 500;
      gsap.to(controls.object.position, {
        duration: 2,
        ease: "power1.inOut",
        x: 13.710529593583965,
        y: 3.3846063691865487,
        z: -0.8073833482647784
      });
      gsap.to(controls.target, {
        duration: 2,
        ease: "power1.inOut",
        x: -0.4306419770217947,
        y: 1.5599397941258784,
        z: 0.027995907248003857
      });
      gsap.to(camera, {
        duration: 3,
        ease: "power1.inOut",
        zoom: 1330,
        onUpdate: function () {
          camera.updateProjectionMatrix();
        }
      });
    }
  }
  useEffect(() => {
    controls.autoRotate = true;
    controls.enableZoom = true;
    controls.maxZoom = 250; //250;
    controls.minZoom = 95;
    return () => {
      controls.dispose();
    };
  });

  useFrame((state, delta) => {
    controls.update();
  });

  return null;
};


/* progression*/
function Loader() {
  const { active, progress } = useProgress();
  setTimeout(() => {
    if (document.getElementById("prctn")) {
      document.getElementById("prctn").innerHTML = progress + "%";
    }
    
    active
      ? console.log(progress)
      : document.querySelector("#overlay2") &&
        document.querySelector("#overlay2").remove();
  }, 3500);
}

export default function App() {

  return (
    <Suspense fallback={Loader()}>
      <Canvas shadows gl={{ antialias: false }}>
        <Preload all />
        <color attach="background" args={["#202030"]} />
        <fog attach="fog" args={["#202030", 10, 19.5]} />
        <hemisphereLight
          intensity={0.15}
          color="#eaeaea"
          position={[0, 1, 0]}
        />
        <pointLight
          intensity={0.15}
          color="#ff8d04"
          position={[-1.8, 0.7, 2.52]}
        />
        <pointLight
          intensity={0.15}
          color="#ff04e8"
          position={[-1.8, -0.7, 2.52]}
        />
        <directionalLight
          castShadow
          intensity={0.2}
          shadow-mapSize={[1024, 1024]}
          shadow-bias={-0.0001}
          position={[10, 10, -10]}
        />
        <PerformanceMonitor >
          <Tower position={[0, -3, 0]}></Tower>
        </PerformanceMonitor>
        <Effects disableGamma>
          <unrealBloomPass threshold={0.8} strength={1} radius={0.5} />
        </Effects>
        <BakeShadows />
        <OrthographicCamera
          makeDefault
          far={100}
          near={0.1}
          position={[10, 2, 10]}
          zoom={100}
        />
        {/* <OrbitControls autoRotate={true} enableZoom={true} maxZoom={250} minZoom={95}  /> */}
        <CameraController />
      </Canvas>
    </Suspense>
  );
}
