  function BufferPoints() {
    var count = 3000;
    let posio = [],
      veloio = [];
    const prtc = useRef();

    const points = useMemo(() => {
      for (let i = 0; i < count; i++) {
        posio.push(
          Math.random() * 20 - 10,
          Math.random() * 10,
          Math.random() * 20 - 10
        );
        veloio.push(
          (Math.random() * 6 - 3) * 0.1,
          (Math.random() * 5 + 0.12) * 0.18,
          (Math.random() * 6 - 3) * 0.1
        );
      }
      return new BufferAttribute(new Float32Array(posio), 3);
    }, [count]);

    useFrame((state, delta) => {
      // console.log(prtc.current.attributes.position.array)
      for (let i = 0; i < count * 3; i += 3) {
        //x
        prtc.current.attributes.position.array[i] -= veloio[i] * delta;
        //y
        prtc.current.attributes.position.array[i + 1] -= veloio[i + 1] * delta;
        //z
        prtc.current.attributes.position.array[i + 2] -= veloio[i + 2] * delta;

        // y<0
        if (prtc.current.attributes.position.array[i + 1] <= -0.5) {
          //x
          prtc.current.attributes.position.array[i] = Math.random() * 20 - 10;
          //y
          prtc.current.attributes.position.array[i + 1] = Math.random() * 10;
          //z
          prtc.current.attributes.position.array[i + 2] =
            Math.random() * 20 - 10;
        }
      }
      prtc.current.attributes.position.needsUpdate = true;
    });

    return (
      <points>
        <bufferGeometry ref={prtc}>
          <bufferAttribute attach={"attributes-position"} {...points} />
        </bufferGeometry>
        <PointMaterial
          size={1.5}
          threshold={0.1}
          fog={true}
          color={0xffffff}
          // map={sprite}
          toneMapped={false}
        />
      </points>
    );
  }
